import React from 'react';
import {Canvas} from "./Canvas";
import {randomInteger} from "./utils";
import Color2 from "color";

const w = 10;

let colors = new Array(1000)
export class Palette extends React.PureComponent {

    componentWillReceiveProps(nextProps) {
        if (nextProps.value !== this.props.value) {

            const ctx = this.refs.canvas.getCtx();
            ctx.fillStyle = nextProps.value;
            const {x, y} = nextProps;

            ctx.fillRect(x, y, w, w);
        }
    }

    handleClick = (...p) => {
        const {onClick, onPip, edited} = this.props;
        if (edited)
            onClick && onClick(...p);
        else {
            const ctx = this.refs.canvas.getCtx();

            onPip && onPip(Color2(ctx.getImageData(p[0].offsetX, p[0].offsetY, 1, 1).data.slice(0, 3)).hex(), Math.floor(p[0].offsetX / w) * w, Math.floor(p[0].offsetY / w) * w);
        }

    };
    handleDraw = (...p) => {
        const {onDraw, edited} = this.props;
        if (edited)
            onDraw && onDraw(...p);

    };
    random = (delta) => {
        const ctx = this.refs.canvas.getCtx();
        colors = new Array(1000);
        for (let i = 0; i < 40; i++) {
            for (let j = 0; j < 25; j++) {
                let color;
                do {
                    const r = randomInteger(Math.max(265 - delta * 3, 0), Math.min(280 + delta * 3, 359));
                    const g = randomInteger(Math.max(60 - delta * delta, 0), Math.min(100 + delta * delta, 100));
                    const b = randomInteger(Math.max(25 - delta / randomInteger(-20, 20), 0), Math.min(75 + delta / randomInteger(-20, 20), 100));
                    color = Color2.hsl([r, g, b]).hex();
                    ctx.fillStyle = color;
                    ctx.fillRect(i*w, j*w, w, w);
                } while (colors.indexOf(color) !== -1)
                colors[i] = color;
            }
        }

    };
    render() {
        return (
            <Canvas
                readonly={!this.props.edited}
                ref="canvas"
                width={40 * w} height={25 * w}
                onClick={this.handleClick}
                onDraw={this.handleDraw}/>
        );
    }
}