import React from "react";

export class Enter extends React.PureComponent {
    order = [0, 2, 3, 0];
    state = {
        i: 0
    };
    handleMouseEnter = i => e => {
        if (i === this.order[this.state.i]) {
            this.setState(state => ({i: state.i + 1}), () => {
                if (this.state.i === 4) {
                    console.log(111111111)
                }
            })
        } else {
            this.setState(state => ({i: 0}), () => {
                console.log(2222222222)
            })
        }
    };

    render() {
        return (
            <div className={"enter"}>
                {[0, 1, 2, 3].map(i => (
                    <div className={"enter-door"} onMouseEnter={this.handleMouseEnter(i)}></div>
                ))}
            </div>
        );
    }

}