import React from 'react';
import {Fills, Tools} from "./utils";
import {Select} from "./Select";

export class Paint extends React.PureComponent {

    handlePaintFillChose = fill => {
        const {onChange, value} = this.props;

        onChange && onChange({
            ...value,
            fill
        });
    };

    render() {
        const {value} = this.props;
        return (
            <div className={"paint"}>
                <div>paint</div>
                <Select
                    items={Object.values(Fills)}
                    value={value.fill}
                    onChange={this.handlePaintFillChose}/>

            </div>
        );
    }
}