import React from 'react';
import {BrushTypes, Fills} from "./utils";
import {Select} from "./Select";

export class Brush extends React.PureComponent {

    handleSizeChange = e => {
        const {onChange, value} = this.props;

        onChange && onChange({
            ...value,
            size: e.target.value
        });
    };

    handleOpacityChang = e => {
        const {onChange, value} = this.props;

        onChange && onChange({...value, opacity: e.target.value});
    };

    selectType = type => {
        const {onChange, value} = this.props;

        onChange && onChange({...value, type});
    };

    render() {
        const {value} = this.props;
        return (
            <div className={"brush"}>
                <div>brush</div>
                <div className={"brushbody"}>
                    <input type="range" min={1} max={100} value={value.size} onChange={this.handleSizeChange}/>
                    <input
                        type="range"
                        min={0} max={1} step={0.05}
                        value={value.opacity}
                        onChange={this.handleOpacityChang}/>

                    <Select
                        items={Object.values(BrushTypes)}
                        value={value.type}
                        onChange={this.selectType}/>
                </div>

            </div>
        );
    }
}