export const ResizeType = {
    zero: "",
    nn: "nearest-neighbor",
    bi: "biliniear-interpolation"
};

export function throttled(delay, fn) {
    let lastCall = 0;
    return function (...args) {
        const now = (new Date).getTime();
        if (now - lastCall < delay) {
            return;
        }
        lastCall = now;
        return fn(...args);
    }
}

export function randomInteger(min, max) {
    var rand = min - 0.5 + Math.random() * (max - min + 1);
    rand = Math.round(rand);
    return rand;
}

export const whiteSheet = ctx => ({w, h}) => {
    const image = ctx.createImageData(w, h);
    for (let i = 0; i < image.data.length; i++)
        image.data[i] = 255;
    return image;
};

export const createPattern = ctx => ({w = 50, h = 50}) => {
    const image = ctx.createImageData(w, h);
    for (let i = 0; i < image.data.length; i++)
        image.data[i] = 255;

    const mask = ctx.createImageData(w, h);
    for (let i = 0; i < mask.data.length; i+=4) {
        mask.data[i] = 0;
        mask.data[i+1] = 0;
        mask.data[i +2] = 0;
        mask.data[i + 3] = 255;
    }
    return {
        w, h,
        image: image,
        mask: mask,
        masked: ctx.createImageData(w, h),
        resizeType: "nn"
    }
};

export const cutCircle = function (context, x, y, radius) {
    context.globalCompositeOperation = 'destination-out';
    context.moveTo(x, y);
    context.beginPath();
    context.arc(x, y, radius, 0, Math.PI * 2, true);
    context.closePath();
    context.fill();
    context.globalCompositeOperation = "source-over";
};
export const circle = function (context, x, y, radius) {
    context.moveTo(x, y);
    context.beginPath();
    context.arc(x, y, radius, 0, Math.PI * 2, true);
    context.closePath();
    context.fill();
};

export const createImage = canvas => {
    return new Promise((resolve, reject) => {
        var tempCanvas = document.createElement("canvas");
        var tempCtx = tempCanvas.getContext("2d");

        tempCanvas.width = canvas.width;
        tempCanvas.height = canvas.height;

        // put the modified pixels on the temp canvas
        tempCtx.drawImage(canvas, 0, 0, canvas.width, canvas.height);

        // use the tempCanvas.toDataURL to create an img object
        var img = new Image();
        img.onload = () => {
            resolve(img)
        };
        img.src = tempCanvas.toDataURL();
    });
};

export function hexToRgbaObject(hex, opacity = 1) {
    var c;
    if (/^#([A-Fa-f0-9]{3}){1,2}$/.test(hex)) {
        c = hex.substring(1).split('');
        if (c.length === 3) {
            c = [c[0], c[0], c[1], c[1], c[2], c[2]];
        }
        c = '0x' + c.join('');
        return {
            r: (c >> 16) & 255,
            g: (c >> 8) & 255,
            b: c & 255,
            a: Math.round(255 * opacity)
        };
    }
    throw new Error('Bad Hex');
}

export function hexToRgbaString(hex, opacity) {
    var c = hexToRgbaObject(hex, opacity);
    return `rgba(${c.r},${c.g},${c.b},${c.a})`
}