export function floodfillmask(data, x, y, maskImageData, tolerance, width, height) {

    const fillcolor = {
        r: 255,
        g: 0,
        b: 0,
        a: 255
    };
    var length = data.length;
    var Q = [];
    var P = [];
    var i = (x + y * width) * 4;
    var e = i, w = i, me, mw, w2 = width * 4;

    var targetcolor = [data[i], data[i + 1], data[i + 2], data[i + 3]];

    const oldData = new Uint8ClampedArray(data);

    if (!pixelCompare(i, targetcolor, fillcolor, data, length, tolerance, width)) {
        return false;
    }
    Q.push(i);
    let kkk = 0;
    while (Q.length) {
        kkk++;
        i = Q.pop();
        if (pixelCompareAndSet(i, targetcolor, fillcolor, data, length, tolerance, width)) {
            P.push(i);
            e = i;
            w = i;
            mw = parseInt(i / w2) * w2 - 1; //left bound
            me = mw + w2 - 1;               //right bound
            while (mw < w && mw < (w -= 4) && pixelCompareAndSet(w, targetcolor, fillcolor, data, length, tolerance, width)) {
                P.push(w)
            } //go left until edge hit
            while (me > e && me > (e += 4) && pixelCompareAndSet(e, targetcolor, fillcolor, data, length, tolerance, width)) {
                P.push(e)
            } //go right until edge hit
            for (var j = w + 4; j < e; j += 4) {
                if (j - w2 >= 0 && pixelCompare(j - w2, targetcolor, fillcolor, data, length, tolerance, width)) Q.push(j - w2); //queue y-1
                if (j + w2 < length && pixelCompare(j + w2, targetcolor, fillcolor, data, length, tolerance, width)) Q.push(j + w2); //queue y+1
            }
        }
    }

    console.log(kkk);

    P.forEach(p => {

        var x = (p / 4) % width % maskImageData.width;
        var y = Math.floor((p / 4) / width) % maskImageData.height;


        var j = (x + y * maskImageData.width) * 4;
        var targetcolor = [0, 0, 0, 255];
        var maskcolor = [maskImageData.data[j], maskImageData.data[j + 1], maskImageData.data[j + 2], maskImageData.data[j + 3]];

        // console.log(maskcolor);
        if (maskcolor[0] === 0 && maskcolor[1] === 0 && maskcolor[2] === 0 && maskcolor[3] === 255) {
            //black
            data[p] = targetcolor[0];
            data[p + 1] = targetcolor[1];
            data[p + 2] = targetcolor[2];
            data[p + 3] = targetcolor[3];
        } else {
            data[p] = oldData[p];
            data[p + 1] = oldData[p + 1];
            data[p + 2] = oldData[p + 2];
            data[p + 3] = oldData[p + 3];
        }
    });


    return data;
}

export function floodfillpatternmask(data, x, y, patternImageData, maskImageData, tolerance, width, height, xMirror, yMirror) {

    const getPatX = !xMirror
        ? (i, width, pWidth) => (i / 4) % width % pWidth
        : (i, width, pWidth) => {
            const x = (i / 4) % width;

            return Math.trunc(x / pWidth) % 2 === 0
                ? x % pWidth
                : pWidth - x % pWidth;
        };
    const getPatY = !yMirror
        ? (i, width, pHeight) => y = Math.floor((i / 4) / width) % pHeight
        : (i, width, pHeight) => {
            const y =  Math.floor((i / 4) / width);

            return Math.trunc(y / pHeight) % 2 === 0
                ? y % pHeight
                : pHeight - y % pHeight;
        };
    const fillcolor = {
        r: 255,
        g: 0,
        b: 0,
        a: 255
    };
    var length = data.length;
    var Q = [];
    var P = [];
    var i = (x + y * width) * 4;
    var e = i, w = i, me, mw, w2 = width * 4;

    var targetcolor = [data[i], data[i + 1], data[i + 2], data[i + 3]];

    const oldData = new Uint8ClampedArray(data);

    if (!pixelCompare(i, targetcolor, fillcolor, data, length, tolerance, width)) {
        return false;
    }
    Q.push(i);
    let kkk = 0;
    while (Q.length) {
        kkk++;
        i = Q.pop();
        if (pixelCompareAndSet(i, targetcolor, fillcolor, data, length, tolerance, width)) {
            P.push(i);
            e = i;
            w = i;
            mw = parseInt(i / w2) * w2 - 1; //left bound
            me = mw + w2 - 1;               //right bound
            while (mw < w && mw < (w -= 4) && pixelCompareAndSet(w, targetcolor, fillcolor, data, length, tolerance, width)) {
                P.push(w)
            } //go left until edge hit
            while (me > e && me > (e += 4) && pixelCompareAndSet(e, targetcolor, fillcolor, data, length, tolerance, width)) {
                P.push(e)
            } //go right until edge hit
            for (var j = w + 4; j < e; j += 4) {
                if (j - w2 >= 0 && pixelCompare(j - w2, targetcolor, fillcolor, data, length, tolerance, width)) Q.push(j - w2); //queue y-1
                if (j + w2 < length && pixelCompare(j + w2, targetcolor, fillcolor, data, length, tolerance, width)) Q.push(j + w2); //queue y+1
            }
        }
    }

    console.log(kkk);

    P.forEach(p => {

        var x = getPatX(p, width, patternImageData.width);
        var y = getPatY(p, width, patternImageData.height);


        var j = (x + y * patternImageData.width) * 4;
        var targetcolor = [patternImageData.data[j], patternImageData.data[j + 1], patternImageData.data[j + 2], patternImageData.data[j + 3]];
        var maskcolor = [maskImageData.data[j], maskImageData.data[j + 1], maskImageData.data[j + 2], maskImageData.data[j + 3]];

        // console.log(maskcolor);
        if (maskcolor[0] === 0 && maskcolor[1] === 0 && maskcolor[2] === 0 && maskcolor[3] === 255) {
            //
            data[p] = targetcolor[0];
            data[p + 1] = targetcolor[1];
            data[p + 2] = targetcolor[2];
            data[p + 3] = targetcolor[3];
        } else {
            data[p] = oldData[p];
            data[p + 1] = oldData[p + 1];
            data[p + 2] = oldData[p + 2];
            data[p + 3] = oldData[p + 3];
        }
    });


    return data;
}

export function floodfillpattern(data, x, y, patternImageData, tolerance, width, height) {

    const fillcolor = {
        r: 255,
        g: 0,
        b: 0,
        a: 255
    };
    var length = data.length;
    var Q = [];
    var P = [];
    var i = (x + y * width) * 4;
    var e = i, w = i, me, mw, w2 = width * 4;

    var targetcolor = [data[i], data[i + 1], data[i + 2], data[i + 3]];

    if (!pixelCompare(i, targetcolor, fillcolor, data, length, tolerance, width)) {
        return false;
    }
    Q.push(i);
    let kkk = 0;
    while (Q.length) {
        kkk++;
        i = Q.pop();
        if (pixelCompareAndSet(i, targetcolor, fillcolor, data, length, tolerance, width)) {
            P.push(i);
            e = i;
            w = i;
            mw = parseInt(i / w2) * w2 - 1; //left bound
            me = mw + w2 - 1;               //right bound
            while (mw < w && mw < (w -= 4) && pixelCompareAndSet(w, targetcolor, fillcolor, data, length, tolerance, width)) {
                P.push(w)
            } //go left until edge hit
            while (me > e && me > (e += 4) && pixelCompareAndSet(e, targetcolor, fillcolor, data, length, tolerance, width)) {
                P.push(e)
            } //go right until edge hit
            for (var j = w + 4; j < e; j += 4) {
                if (j - w2 >= 0 && pixelCompare(j - w2, targetcolor, fillcolor, data, length, tolerance, width)) Q.push(j - w2); //queue y-1
                if (j + w2 < length && pixelCompare(j + w2, targetcolor, fillcolor, data, length, tolerance, width)) Q.push(j + w2); //queue y+1
            }
        }
    }

    console.log(kkk);

    P.forEach(p => {

        var x = (p / 4) % width % patternImageData.width;
        var y = Math.floor((p / 4) / width) % patternImageData.height;


        var j = (x + y * patternImageData.width) * 4;
        var targetcolor = [patternImageData.data[j], patternImageData.data[j + 1], patternImageData.data[j + 2], patternImageData.data[j + 3]];


        data[p] = targetcolor[0];
        data[p + 1] = targetcolor[1];
        data[p + 2] = targetcolor[2];
        data[p + 3] = targetcolor[3];
    });


    return data;
}

function pixelCompare(i, targetcolor, fillcolor, data, length, tolerance, width) {
    if (i < 0 || i >= length) return false; //out of bounds
    if (data[i + 3] === 0 && fillcolor.a > 0) return true;  //surface is invisible and fill is visible

    if (
        Math.abs(targetcolor[3] - fillcolor.a) <= tolerance &&
        Math.abs(targetcolor[0] - fillcolor.r) <= tolerance &&
        Math.abs(targetcolor[1] - fillcolor.g) <= tolerance &&
        Math.abs(targetcolor[2] - fillcolor.b) <= tolerance
    ) return false; //target is same as fill

    if (
        (targetcolor[3] === data[i + 3]) &&
        (targetcolor[0] === data[i]) &&
        (targetcolor[1] === data[i + 1]) &&
        (targetcolor[2] === data[i + 2])
    ) return true; //target matches surface

    if (
        Math.abs(targetcolor[3] - data[i + 3]) <= (255 - tolerance) &&
        Math.abs(targetcolor[0] - data[i]) <= tolerance &&
        Math.abs(targetcolor[1] - data[i + 1]) <= tolerance &&
        Math.abs(targetcolor[2] - data[i + 2]) <= tolerance
    ) return true; //target to surface within tolerance

    return false; //no match
}

function pixelCompareAndSet(i, targetcolor, fillcolor, data, length, tolerance, width) {
    if (pixelCompare(i, targetcolor, fillcolor, data, length, tolerance)) {
        //fill the color
        data[i] = fillcolor.r;
        data[i + 1] = fillcolor.g;
        data[i + 2] = fillcolor.b;
        data[i + 3] = fillcolor.a;
        return true;
    }
    return false;
}