import React, {Component} from 'react';
import './App.css';
import {Canvas} from "./components/Canvas";
import {Brush} from "./components/Brush";
import {Color} from "./components/Color";
import {PatternEditor} from "./components/PatternEditor";
import {floodfill} from "./components/floodfill";
import {floodfillmask, floodfillpatternmask} from "./components/floodfillpattern";
import {
    Tools,
    Fills,
    hexToRgbaObject,
    hexToRgbaString,
    BrushTypes,
    createPattern,
    circle,
    cutCircle, whiteSheet, randomInteger, throttled
} from "./components/utils";
import {Paint} from "./components/Paint";
import {Line} from "./components/Line";
import {HotKeys, GlobalHotKeys} from "react-hotkeys";
import Color2 from "color";
import {Select} from "./components/Select";
import _omit from "lodash/omit";
import {Enter} from "./components/Enter";

/**
 *
 *
 *
 *
 *
 *
 *
 *    
 * enter
   ()
 *
 *
 *
 *
 *    
 *

 mouse trottling

 *
 * */


class App extends Component {


    componentDidMount() {



        document.addEventListener("mousemove", throttled(100, this.move));
        document.addEventListener("keydown", this.keydownHandler);
        document.addEventListener("keyup", this.keyupHandler);

        this.setState(state => ({
            patterns: state.patterns.map(createPattern(this.refs.canvas.ctx)),
            canvas: {...state.canvas, image: whiteSheet(this.refs.canvas.ctx)(state.canvas)}
        }));
        setTimeout(() => console.log(this.state), 0)
    }

    state = {
        keys: {},
        canvas: {
            w: 400,
            h: 400,
        },
        brush: {
            size: 8,
            opacity: 1,
            type: BrushTypes.Circle
        },
        line: {
            size: 8,
            opacity: 1,
        },
        color: {
            value: '#000000'
        },
        paint: {
            fill: Fills.Color
        },
        patterns: [{
            w: 50,
            h: 62,
            resizeType: "zero"
        }, {
            w: 50,
            h: 62,
            resizeType: "zero"
        }],
        currentPattern: 0,
        currentTool: Tools.Brush
    };

    getRGBAColor = () =>
        hexToRgbaObject(this.state.color.value, this.state.color.opacity);

    handleCanvasChange = image => this.setState(state => ({canvas: {...state.canvas, image}}));

    handleBrushChange = brush => this.setState({brush});//, () => console.log(this.state.brush));
    handleColorChange = color => this.setState({color});//, () => console.log(this.state.color));
    handlePaintChange = paint => this.setState({paint});//, () => console.log(this.state.color));
    handleLineChange = line => this.setState({line});//, () => console.log(this.state.color));

    handlePatternChange = (n) => pattern => {
        this.setState(state => ({
            patterns: state.patterns.map((p, i) => i === n ? pattern : p)
        }), () => console.log(this.state.patterns[n]))
    };
    handleAddPattern = e => {
        this.setState(state => ({
            patterns: [...state.patterns, createPattern(this.refs.canvas.ctx)({
                w: 50,
                h: 50,
            })]
        }))
    };

    handleCurrentPatternChose = pattern => e => this.setState({currentPattern: pattern});


    handleToolChose = tool => this.setState({currentTool: tool});

    drawProcessing = (e, ctx, canvas, prevE) => {
        if (this.state.currentTool === Tools.Brush) {
            ctx.fillStyle = hexToRgbaString(this.state.color.value);
            ctx.globalAlpha = this.state.brush.opacity;

            if (this.state.brush.type === BrushTypes.Square) {
                ctx.fillRect(e.offsetX - this.state.brush.size / 2, e.offsetY - this.state.brush.size / 2, this.state.brush.size, this.state.brush.size);
            } else if (this.state.brush.type === BrushTypes.Circle) {
                ctx.beginPath();
                ctx.arc(e.offsetX, e.offsetY, this.state.brush.size / 2, 0, 2 * Math.PI);
                ctx.fill();
            } else if (this.state.brush.type === BrushTypes.Pattern) {
                const p = this.state.patterns[this.state.currentPattern].image;
                const i = this.state.patterns[this.state.currentPattern].imageMasked;
                ctx.drawImage(i, e.offsetX - p.width / 2, e.offsetY - p.height / 2, p.width, p.height);
            }
            ctx.globalAlpha = 1;

        } else if (this.state.currentTool === Tools.Line) {
            if (prevE) {
                ctx.globalAlpha = this.state.line.opacity;
                ctx.beginPath();
                ctx.moveTo(prevE.offsetX, prevE.offsetY);
                ctx.lineTo(e.offsetX, e.offsetY);
                ctx.strokeStyle = this.state.color.value;
                ctx.lineWidth = this.state.line.size;
                ctx.stroke();
                ctx.globalAlpha = 1;
                ctx.closePath();
            }
        }
    };

    clickProcessing = (e, ctx, canvas) => {
        if (this.state.currentTool === Tools.Paint) {
            if (this.state.paint.fill === Fills.Color) {
                console.log(1);
                const imda = ctx.getImageData(0, 0, this.state.canvas.w, this.state.canvas.h);

                let data = imda.data;
                let width = imda.width;
                let height = imda.height;
                let color = hexToRgbaObject(this.state.color.value);

                floodfill(data, e.offsetX, e.offsetY, color, 0, width, height);

                ctx.putImageData(imda, 0, 0);
            } else if (this.state.paint.fill === Fills.Pattern) {
                const imda = ctx.getImageData(0, 0, this.state.canvas.w, this.state.canvas.h);

                let data = imda.data;
                let width = imda.width;
                let height = imda.height;

                const pat = this.state.patterns[this.state.currentPattern];

                floodfillpatternmask(data, e.offsetX, e.offsetY, pat.image, pat.mask, 0, width, height, pat.xMirror, pat.yMirror);

                ctx.putImageData(imda, 0, 0);
            }
        }
    };

    maskDrawProcessing = (e, ctx, canvas, prevE) => {
        if (this.state.currentTool === Tools.Brush) {
            if (this.state.brush.type === BrushTypes.Pattern) {
                const p = this.state.patterns[this.state.currentPattern].mask;
                const i = this.state.patterns[this.state.currentPattern].imageMask;
                ctx.drawImage(i, e.offsetX - p.width / 2, e.offsetY - p.height / 2, p.width, p.height);
            } else {
                if (this.state.color.value.toLocaleLowerCase() === "#ffffff") {
                    if (this.state.brush.type === BrushTypes.Square) {
                        ctx.clearRect(e.offsetX - this.state.brush.size / 2, e.offsetY - this.state.brush.size / 2, this.state.brush.size, this.state.brush.size);
                    } else if (this.state.brush.type === BrushTypes.Circle) {
                        cutCircle(ctx, e.offsetX, e.offsetY, this.state.brush.size / 2);
                    }
                } else {
                    ctx.fillStyle = "#000000";
                    if (this.state.brush.type === BrushTypes.Square) {
                        ctx.fillRect(e.offsetX - this.state.brush.size / 2, e.offsetY - this.state.brush.size / 2, this.state.brush.size, this.state.brush.size);
                    } else if (this.state.brush.type === BrushTypes.Circle) {
                        circle(ctx, e.offsetX, e.offsetY, this.state.brush.size / 2);
                    }
                }
            }
        } else if (this.state.currentTool === Tools.Line) {
            if (prevE) {

                if (this.state.color.value.toLocaleLowerCase() === "#ffffff") {

                    ctx.globalCompositeOperation = 'destination-out';
                    ctx.beginPath();
                    ctx.moveTo(prevE.offsetX, prevE.offsetY);
                    ctx.lineTo(e.offsetX, e.offsetY);
                    ctx.strokeStyle = 'black';
                    ctx.lineWidth = this.state.line.size;
                    ctx.stroke();
                    ctx.closePath();
                    ctx.globalCompositeOperation = "source-over";
                } else {
                    ctx.beginPath();
                    ctx.moveTo(prevE.offsetX, prevE.offsetY);
                    ctx.lineTo(e.offsetX, e.offsetY);
                    ctx.strokeStyle = 'black';
                    ctx.lineWidth = this.state.line.size;
                    ctx.stroke();
                    ctx.closePath();
                }
            }
        }
    };

    maskClickProcessing = (e, ctx, canvas) => {
        if (this.state.currentTool === Tools.Paint) {
            if (this.state.paint.fill === Fills.Color) {
                const imda = ctx.getImageData(0, 0, this.state.canvas.w, this.state.canvas.h);

                let data = imda.data;
                let width = imda.width;
                let height = imda.height;
                let color = this.state.color.value.toLocaleLowerCase() === "#ffffff"
                    ? {r: 0, g: 0, b: 0, a: 0}
                    : {r: 0, g: 0, b: 0, a: 255}

                floodfill(data, e.offsetX, e.offsetY, color, 55, width, height);

                ctx.putImageData(imda, 0, 0);
            } else if (this.state.paint.fill === Fills.Pattern) {
                const imda = ctx.getImageData(0, 0, this.state.canvas.w, this.state.canvas.h);

                let data = imda.data;
                let width = imda.width;
                let height = imda.height;

                floodfillmask(data, e.offsetX, e.offsetY, this.state.patterns[this.state.currentPattern].mask, 0, width, height);

                ctx.putImageData(imda, 0, 0);
            }
        }
    };

    save = () => {
        var dataURL = this.refs.canvas.canvas.toDataURL("image/jpeg");
        var link = document.createElement("a");
        document.body.appendChild(link); // Firefox requires the link to be in the body :(
        link.href = dataURL;
        link.download = "ZzZzZzZ.jpg";
        link.click();
        document.body.removeChild(link);
    };

    keyMap = {
        PREV: "1",
        NEXT: "2",
        BRUSH: "q",
        BRUSH_S: "w",
        BRUSH_C: "e",
        BRUSH_P: "r",
        PAINT: "a",
        PAINT_C: "s",
        PAINT_P: "d",
        LINE: "z",
        WHITE: "x",
        BLACK: "c",
        RAND: "v",

    };

    hotKeysHandlers = {
        NEXT: () =>
            this.setState(state => ({currentPattern: (state.currentPattern + 1) % state.patterns.length})),
        PREV: () =>
            this.setState(state => ({currentPattern: (state.currentPattern - 1 + state.patterns.length) % state.patterns.length})),
        BRUSH: () =>
            this.setState({currentTool: Tools.Brush}),
        BRUSH_S: () =>
            this.setState(state => ({brush: {...state.brush, type: BrushTypes.Square}})),
        BRUSH_C: () =>
            this.setState(state => ({brush: {...state.brush, type: BrushTypes.Circle}})),
        BRUSH_P: () =>
            this.setState(state => ({brush: {...state.brush, type: BrushTypes.Pattern}})),
        PAINT: () =>
            this.setState({currentTool: Tools.Paint}),
        PAINT_C: () =>
            this.setState(state => ({paint: {...state.paint, fill: Fills.Color}})),
        PAINT_P: () =>
            this.setState(state => ({paint: {...state.paint, fill: Fills.Pattern}})),
        LINE: () =>
            this.setState({currentTool: Tools.Line}),
        WHITE: () =>
            this.setState(state => ({color: {...state.color, value: "#ffffff"}})),
        BLACK: () =>
            this.setState(state => ({color: {...state.color, value: "#000000"}})),
        RAND: () =>
            this.setState(state => {
                const r = randomInteger(0, 255);
                const g = randomInteger(0, 255);
                const b = randomInteger(0, 255);
                const color = Color2.rgb([r, g, b]).hex();
                return {color: {...state.color, value: color}}
            }),
    };

    keydownHandler = e => {
        const code = e.key.charCodeAt(0);
        console.log(e);

        if (!this.state.keys[code]) {

            this.setState(state => ({keys: {...state.keys, [code]: true}}), () => console.log(this.state.keys));
        }
    };

    keyupHandler = (e) => {
        const code = e.key.charCodeAt(0);

        this.setState(state => ({keys: _omit(state.keys, code)}), () => console.log(this.state.keys));
    };

    move = e => {
        if (this.state.keys[70] || this.state.keys[102]) {

        }
    }

    render() {
        return (

            <GlobalHotKeys keyMap={this.keyMap} handlers={this.hotKeysHandlers}>
                <HotKeys>
                    <div className={"all"}>
                        <div className="App">
                            <div className={"controls"}>
                                <div className={"kunteynir"}>
                                    {/*PatternCurr:{Object.keys(this.state.patterns).map(pattern => (*/}
                                    {/*<button key={pattern}*/}
                                            {/*onClick={this.handleCurrentPatternChose(pattern)}>{pattern}</button>*/}

                                {/*))}:{this.state.currentPattern}*/}

                                    <Select
                                        items={Object.values(Tools)}
                                        value={this.state.currentTool}
                                        onChange={this.handleToolChose}/>
                                    {this.state.currentTool === Tools.Brush &&
                                    <Brush
                                        value={this.state.brush}
                                        onChange={this.handleBrushChange}/>}
                                    {this.state.currentTool === Tools.Paint &&
                                    <Paint
                                        value={this.state.paint}
                                        onChange={this.handlePaintChange}/>}
                                    {this.state.currentTool === Tools.Line &&
                                    <Line
                                        value={this.state.line}
                                        onChange={this.handleLineChange}/>}
                                    <Color
                                        onClick={this.clickProcessing}
                                        onDraw={this.drawProcessing}
                                        value={this.state.color}
                                        onChange={this.handleColorChange}/>
                                </div>
                            </div>
                            <div className={"patterns"}>
                                {this.state.patterns.map((pattern, index) => (
                                    <PatternEditor
                                        chosen={index === this.state.currentPattern}
                                        onChose={this.handleCurrentPatternChose(index)}
                                        key={index}
                                        onPatternDraw={this.drawProcessing}
                                        onPatternClick={this.clickProcessing}
                                        onMaskDraw={this.maskDrawProcessing}
                                        onMaskClick={this.maskClickProcessing}
                                        name={index}
                                        value={pattern}
                                        onChange={this.handlePatternChange(index)}/>
                                ))}
                                <div className={"patternEditor"}>
                                    <Canvas
                                        ref="canvas"
                                        width={this.state.canvas.w}
                                        height={this.state.canvas.h}
                                        value={this.state.canvas.image}
                                        onChange={this.handleCanvasChange}
                                        onClick={this.clickProcessing}
                                        onDraw={this.drawProcessing}/>
                                    <div className={"btns"}>
                                        <button onClick={this.handleAddPattern}>+</button>
                                        <button onClick={this.save}>save</button>
                                    </div>

                                </div>
                            </div>

                            <Enter/>

                            {/*<div>*/}
                            {/*{tp.map(p => (*/}
                            {/*<div style={{*/}
                            {/*display: "inline-block",*/}
                            {/*width: 15,*/}
                            {/*height: 15,*/}
                            {/*backgroundColor: p*/}
                            {/*}}/>*/}
                            {/*))}*/}
                            {/*</div>*/}
                        </div>
                    </div>
                </HotKeys>
            </GlobalHotKeys>
        );
    }
}

export default App;
