import React from 'react';
import Color2 from "color";
import {randomInteger} from "./utils";
import {Palette} from "./Palette";


let tp = new Array(1000);

for (let i = 0; i < 1000; i++) {
    let color;
    do {
        const r = randomInteger(150, 180);
        const g = randomInteger(40, 60);
        const b = randomInteger(200, 250);
        color = `rgb(${r},${g},${b})`
    } while (tp.indexOf(color) !== -1)
    tp[i] = color;
}

console.log(tp);


export class Color extends React.PureComponent {

    constructor(props) {
        super(props);

        const {value} = props;


        this.state = {
            value: Color2(value.value).hsl(),
            colors: [],
            currentColor: 0,
            p1000: 150,
            palette: false,
            paletteX: 0,
            paletteY: 0,
            paletteEdit: false
        };

    }

    componentWillReceiveProps(nextProps) {
        if (nextProps.value.value !== this.props.value.value) {
            let colors = this.state.colors;
            colors[this.state.currentColor] = Color2(nextProps.value.value).hex()
            this.setState({
                value: Color2(nextProps.value.value).hsl(),
                colors
            });
        }
    }


    // handleValueChange = e => {
    //     const {onChange, value} = this.props;
    //
    //     onChange && onChange({...value, value: e.target.value});
    // };
    handleChoseColor = (c, index) => e => {
        this.setState({currentColor: index}, () => {
            const {onChange, value} = this.props;

            onChange && onChange({...value, value: c});
        });
    };

    handleChangeHSL = (i) => e => {
        const val = +e.target.value;
        this.setState(state => {
            let value = [state.value.color[0], state.value.color[1], state.value.color[2]];
            value[i] = val;
            return {
                value: Color2.hsl(value)
            }
        }, () => {

            const {onChange, value} = this.props;

            onChange && onChange({...value, value: this.state.value.hex()});
        });


    };

    p1000 = (e) => {
        const delta = (this.state.p1000 / 4 - 67 + 100) % 100;

        this.refs.palette.random(delta);
    };

    p1000change = e => {
        const delta = +e.target.value;
        this.setState({p1000: delta});
    };

    togglePalette = e => this.setState(state => ({palette: !state.palette}));


    handlePip = (color, x, y) =>
        this.setState({
            paletteX: x,
            paletteY: y
        }, () => {

            const {onChange, value} = this.props;

            onChange && onChange({...value, value: color});
        });

    paletteEdit = () => this.setState(state => ({paletteEdit: !state.paletteEdit}));

    render() {
        const {value, onDraw, onClick} = this.props;
        return (
            <div className={"color"}>
                <div>color</div>
                <div className={"flex"}>
                    <div className={"colorValue"} style={{
                        backgroundColor: value.value
                    }}/>
                    {/*<input*/}
                    {/*type="color"*/}
                    {/*value={value.value}*/}
                    {/*onChange={this.handleValueChange}/>*/}
                    <input type={"range"} min={0} max={359} step={1}
                           onChange={this.handleChangeHSL(0)}
                           value={this.state.value.color[0]}/>
                    <input type={"range"} min={0} max={100} step={1}
                           onChange={this.handleChangeHSL(1)}
                           value={this.state.value.color[1]}/>
                    <input type={"range"} min={0} max={100} step={1}
                           onChange={this.handleChangeHSL(2)}
                           value={this.state.value.color[2]}/>
                    <div>
                        <button onClick={this.togglePalette}>p</button>
                    </div>
                </div>
                <div className={"randomnes" + (this.state.palette ? "" : " randomes-hide")}>
                    <div className={"flex"}>
                        <button onClick={this.p1000} className={"randomnes-b"}>r</button>
                        <input type={"range"} min={0} max={400} step={1} onChange={this.p1000change}
                               value={this.state.p1000}/>
                        <button onClick={this.paletteEdit}
                                className={"buttonControle"}>edit{this.state.paletteEdit ? "*" : ""}</button>
                    </div>
                    <div className={"palette"}>
                        <Palette ref={"palette"}
                                 edited={this.state.paletteEdit}
                                 onDraw={onDraw}
                                 onClick={onClick}
                                 onPip={this.handlePip} x={this.state.paletteX} y={this.state.paletteY}
                                 value={value.value}/>
                    </div>
                    {/*<div className={"palette"}>*/}
                    {/*{this.state.colors.map((p, index) => (*/}
                    {/*<div*/}
                    {/*onClick={this.handleChoseColor(p, index)}*/}
                    {/*key={p}*/}
                    {/*style={{*/}
                    {/*backgroundColor: p*/}
                    {/*}}/>*/}
                    {/*))}*/}
                    {/*</div>*/}
                </div>
            </div>
        );
    }
}